body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

table,
th,
td {
  border: 1px solid white;
  padding: 8px;
}

.answer_log {
  min-height: 30vh;
  max-height: 30vh;
  overflow-y: auto;
  text-align: left;
  display: flex;
  flex-direction: column-reverse;
  flex-grow: 10;
  padding-top: 8px;
}

.green_text {
  color: greenyellow;
}

.cyan_text {
  color: cyan;
}

.question_box {
  padding: 36px;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: row;
}

.non_image_question_part {
  margin: 16px;
}

.leaderboard_wrapper {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 1;
}

.top_bar {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.credits {
  position: fixed;
  z-index: 999;
  bottom: 0px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.credit_images {
  padding: 0 16px 0 16px;
}
